<template>
  <div class="vFormAfiliacion">
  <div v-if="autorizado" id="sellos">
    <h2 class="AfiliacionTitle">Registro de <span>POS</span></h2>
    <div>
        <b-form class="vFormAfilia" @submit="onSubmit" @reset="onReset" >
        <div>
          <div>
          <b-form-group horizontal  id="dniInputGroup1" label="DNI:" label-for="dniInput1">
             <b-form-input id="dniInput1" ref="dni" 
                           type="text"  maxlength="8" v-model="form.dni" 
                           :state="!$v.form.dni.$invalid"                           
                           required placeholder="Ingrese DNI(########)"
                           @keydown.native = "validaNum2" 
                           @keypress.native = "validaNum" 
                            class="form-control1 form-dni" pattern="[0-9]*" @blur.native="cargarDNI()"> </b-form-input>
              <label class="sNombreAfilia" >{{ form.name   }}</label>
          </b-form-group>

<!-- 24957458  -->

          <b-form-group horizontal id="inputGroup4" label="Nmero de Cuenta:" label-for="nroCuentaInput"> 
            <b-form-select id="nroCuentaInput"
                          :options="cuentas"
                          required
                          v-model="form.cuentas" class="mb-3 form-select1"> </b-form-select>
          </b-form-group>

          <b-form-group horizontal id="seriePOSInputGroup" label="Serie de POS:" label-for="seriePOSInput"> 
            <b-form-select id="seriePOSInput"
                          :options="posDisponible"
                          required
                          v-model="form.seriepos" class="mb-3 form-select1"> </b-form-select>
          </b-form-group>

          <b-form-group horizontal id="nroTicketInputGroup" label="Nmero de Ticket/Voucher:" label-for="nroTicketInput"> 
            <b-form-select id="nroTicketInput"
                          :options="voucherDisponible"
                          required
                          v-model="form.voucher" class="mb-3 form-select1"> </b-form-select>
          </b-form-group>

          <b-form-group horizontal id="inputGroup3" label="Nmero de Crdito:" label-for="nroCreditoInput"> 
            <b-form-input id="nroCreditoInput" 
                          type="text" 
                          maxlength = "12"
                          v-model= "form.credito" 
                          @keydown.native = "validaNum2" 
                          @keypress.native = "validaNum" 
                          @blur.native="cargarNumCredito"
                          placeholder="Ingrese nmero crdito"
                          class="form-control1"> </b-form-input>
          </b-form-group>
          </div>
        </div>
          <div class="row btnAfiliacion">
            <div class="col-md-6">
              <b-button type="submit" variant="primary" class="mx-auto bg-info">Enviar</b-button>
            </div>
            <div class="col-md-6 centered">
              <b-button type="reset" variant="danger" >Limpiar</b-button>
            </div>
          </div>
          <div>
            <a class="completarReg" :href= 'this.$root.$ls.get("VendeMas")' target="_blank"> 
            Completar afiliacin en VENDEMAS</a>
          </div>
        </b-form>
      </div>
  </div>
  <div v-else>
     <p>No esta autorizado. Por favor, inicie sesin.</p>
  </div>
  </div>
</template>

<script>
import fbApp from '../fbApp.js';
import axios from 'axios';

import { validationMixin } from "vuelidate"
import { required, minLength } from "vuelidate/lib/validators"

let dbF = fbApp.firestore();
const sett = {timestampsInSnapshots: true};
dbF.settings(sett);
let listadoVoucher = dbF.collection('voucher');
let listadoCliente = dbF.collection('cliente');

export default {
    name: 'Afiliacion',
    firestore:{
      lstVoucher : listadoVoucher,
      lstCliente : listadoCliente
    },    
    data() {      
        return {
            completado:false,
            datos: []  ,
            form: {
                dni: '',
                name: '',
                credito: '',
                voucher: '',
                seriepos: '',
                cuentas: '',                
            },
            cuentas: [
                { text: 'Elija la cuenta', value: null }
            ],
            posDisponible:[],
            voucherDisponible:[],
            show: this.$store.state.auth,
            dni:null,
            agencia: this.$root.$data.codAgencia,
            lstVoucher: listadoVoucher,
            lstCliente: listadoCliente

        }
    },
    mixins: [
      validationMixin
    ],
    validations: {
      form: {
        credito: {
          required
        },
        dni: {
          required,
          minLength: minLength(7)
        }
      }
    },    
    computed: {
      dniValidado(){        
        return this.form.dni + '00';
      },
      autorizado(){
        //this.show = this.$store.state.auth;
        return this.$store.state.auth;
      }
    },
  methods:{
    validaNum2(event){
      var s = event.keyCode || event.charCode
      /*
      var c = event.which,//c dfgdfgdfgdfg
              d = event.keyCode,
              e = String.fromCharCode(c).toLowerCase(),
              f = "0123456789";
        (-1 != f.indexOf(e) || 9 == d || 37 != c && 37 == d || 39 == d && 39 != c || 8 == d || 46 == d && 46 != c) && 161 != c || event.preventDefault()
      */
      //console.log(s);
      if (s == 190 || s == 222){
        event.preventDefault(true);
        event.stopPropagation(true);
        event.stopped = true;  
      }

      if (event.defaultPrevented) {
        return; // Should do nothing if the default action has been cancelled
      }

      var handled = false;
      if (event.key !== undefined) {
        // Handle the event with KeyboardEvent.key and set handled true.
      } else if (event.keyIdentifier !== undefined) {
        // Handle the event with KeyboardEvent.keyIdentifier and set handled true.
      } else if (event.keyCode !== undefined) {
        // Handle the event with KeyboardEvent.keyCode and set handled true.
      }

      if (handled) {
        // Suppress "double action" if event handled
        event.preventDefault();
      }      
      
    },
    validaNum(event){
      //console.log(event.keyCode || event.charCode);
      //console.log(event.which);
      var s = event.keyCode || event.charCode
      switch (s){
        case 46:
        case 13:
        case 8:
        case 35:
        case 36:
        case 37:
        case 38:
        case 39:
        case 40:
        case 48:
        case 49:
        case 50:
        case 51:
        case 52:
        case 53:
        case 54:
        case 55:
        case 56:
        case 57:        
          break;
        default:
          event.preventDefault();
          event.stopPropagation();
          event.stopped = true;  
          break; 
      }
    },
    updPOS(serial){
        var xxx = this;
        //console.log(xxx.form.dni);
        fbApp.firestore().collection("pos").where("serial", "==", serial)
            .get()
            .then(function(querySnapshot) {            
                querySnapshot.forEach(function(doc) {
                    doc.ref.update({"cliente":xxx.form.dni});
                });
            })
            .catch(/*function(error) {
                console.log("Error getting documents: ", error);
            }*/); 

    },    
    updVoucher(voucher){
        //console.log(voucher);
        fbApp.firestore().collection("voucher").where("sNumTRX", "==", voucher)
            .get()
            .then(function(querySnapshot) {            
                querySnapshot.forEach(function(doc) {
                    doc.ref.update({disponible:0});
                });
            })
            .catch(/*function(error) {
                console.log("Error getting documents: ", error);
            }*/); 

    },
    cargarNumCredito(){
      var mmm = this;
      if (this.form.dni != ""){
        axios.post(mmm.$root.$ls.get("IP") + '/vendemas/validancredito', {
          sDNI: this.form.dni,
          sNumeroCredito: this.form.credito
        })
        .then(function (response) {
          //console.log(response.data);
          mmm.form.name = response.data["sNombres"];
          mmm.cargarCuentas();
          mmm.cargarPOS();
          mmm.cargarVoucher();
          //var nombre = response.data[0];
          // console.log(response.data['sDNI']);
        })
        .catch(/*function (error) {
          console.log(error);
        }*/);      
        //console.log(this.form.dni);

      }
    },
    cargarDNI(){
      var mmm = this;
      if (this.form.dni != ""){
        axios.post(mmm.$root.$ls.get("IP") + '/vendemas/consultadni', {
          sDNI: this.form.dni
        })
        .then(function (response) {
          mmm.form.name = response.data["sNombres"];
          mmm.cargarCuentas();
          mmm.cargarPOS();
          mmm.cargarVoucher();
          //var nombre = response.data[0];
          // console.log(response.data['sDNI']);
        })
        .catch(/*function (error) {
          console.log(error);
        }*/);      
        //console.log(this.form.dni);

      }
    },/*24957458    */
    cargarCuentas(){
      //console.log("cargarCuentas");
      var mmm = this;
      axios.post(mmm.$root.$ls.get("IP") + '/vendemas/consultacuentas', {
        sDNI: this.form.dni
      })
      .then(function (response) {
        //mmm.form.cuentas = response.data["sCuenta"];
        //var nombre = response.data[0];
        var tLista=[];
        response.data.forEach( function(valor) {
           tLista.push({text: valor["sCuenta"], value: valor["sCuenta"]});
           //console.log("En el ndice " + indice + " hay este valor: " + valor["sCuenta"]);
        });       
        //mmm.form.cuentas = tLista; 
        mmm.cuentas = tLista; 
        //console.log(tLista);
      })
      .catch(/*function (error) {
        console.log(error);
      }*/);      
      //console.log(this.form.dni);
    },
    cargarPOS(){
        //console.log("cargarPOS");
        //console.log(this.$root.$data.codAgencia);
        var xxx = this;
        fbApp.firestore().collection("pos").where("agencia", "==", this.agencia).where("cliente","==","").orderBy("serial","asc")
            .get()
            .then(function(querySnapshot) {            
                var tList = [];
                querySnapshot.forEach(function(doc) {
                    //console.log(doc.data());
                    tList.push({text: doc.data()["serial"], value: doc.data()["serial"]});
                });
                xxx.posDisponible = tList;
            })
            .catch( {
               // console.log("Error getting documents: ", error);
            });    
    },   
    cargarVoucher(){
        var xxx = this;
        fbApp.firestore().collection("voucher").where("disponible", "==", 1).orderBy("sNumTRX","asc")
            .get()
            .then(function(querySnapshot) {            
                var tList = [];
                querySnapshot.forEach(function(doc) {
                    //console.log(doc.data());
                    tList.push({text: doc.data()["sNumTRX"], value: doc.data()["sNumTRX"]});
                });
                xxx.voucherDisponible = tList;
            })
            .catch();   
    },
    onSubmit (evt) {
      evt.preventDefault();
      //console.log("zzzzz");
      listadoCliente.add(this.form);
      //console.log("listadoCliente OK");
      this.updVoucher(this.form.voucher);
      //console.log("updVoucher OK");
      this.updPOS(this.form.seriepos);
      //console.log("updPOS OK");
      //alert(JSON.stringify(this.form));
      alert("Afiliacion completada");
      window.open(this.$root.$ls.get("VendeMas"), '_blank');
      this.clearAfiliacion(evt);
      this.completado = true;
    },
    clearAfiliacion(evt){
      evt.preventDefault();
      this.completado = false;
      /* Reset our form values */
      this.form.dni = '';
      this.form.name = '';
      this.form.seriepos = '';
      this.form.voucher='';
      this.form.cuentas = [];
      this.form.credito = '';
      this.form.posDisponible = [];
      this.form.voucherDisponible=[];

/* Trick to reset/clear native browser form validation state */
      this.show = false;
      
      this.$nextTick(() => { this.show = true });
      this.$refs.dni.$el.focus()

    },
    onReset(evt) {
      this.clearAfiliacion(evt);
    }
  }    
}
</script>

<style scoped>
  .btnAfiliacion{
    margin-top: 6%;
  }
  label, .col-form-label{
    color: white!important;
  }
  .vFormAfiliacion{
    /*background-color: #77695e;*/
    width: 80%;
    position: relative;
    top: 0rem;
    left: 80px;
    margin: auto;
    height: 100%;
  }
  .posItem{
    border-style: dashed;
  }    
  .AfiliacionTitle{
    color:white;
  }
  .vLabelAfiliacion{
    color: white;
    display: -webkit-inline-box;
  }
  .form-dni{
    width: 13em;
    display: block;
    /* left: 10%; */
  }
  #nombreClienteInputGroup1{
    display: flex;
  }  
  #nombreClienteInputGroup2{
    display: flex;
  }
  .sNombreAfilia{
    /* float: right; */
    /* margin-right: 34%; */
    /* margin-left: 4%; */
    /* margin-top: 0.4rem; */
    display: flex;
  }
  .form-row{
    color: white;
  }
  .vFormAfilia{
    color: white;
  }
  .completarReg{
    background-color: #5d037b;
    padding: 10px;
    border-radius: 13px;
    text-decoration: none;
    width: 300px;
    color: blanchedalmond;
    cursor: auto;
  }
  .completarReg:hover{
    background-color: #5d037b;
    cursor: pointer;
    padding: 10px;
    border-radius: 13px;
    text-decoration: none;
    width: 300px;
    color: #619279;
    font-weight: bolder
  }
  .btn-danger{
    margin: 4%;    
  }
  @media (max-width: 400px) and (orientation:portrait) {
    .AfiliacionTitle{
      display:none;
    }
  }
</style>