<template>
  <div id="app0" v-show="$root.$data.mostrar">    
    <Cabecera ref="cabecera"></Cabecera>
    <!-- <Login></Login>-->

    <div class="vApp">
      <div v-if="autorizado" class="txtPerfil">
        <span>AGENCIA: <strong> {{ $codAgencia }} - {{ $nomAgencia }}</strong> </span><br>
        <span>PERFIL: <strong > {{ $nomPerfil }} </strong> </span> <br>
        <span>STOCK: <strong> {{ $root.$data.sstock }}       </strong></span>
      </div>
      <div v-if="autorizado" class="userConectado">
        <img class="userPhoto" :src="this.conectadoPhoto"/>
        <strong class="userName"> {{ conectado }} </strong>
      </div>

    </div>
    <div v-if="autorizado" class="vForm">
      <div class="vSubMenu">
          <img class="vendemasImg" src="./assets/vendemas.png">
          <span v-if="bPrincipal">&nbsp;</span>
          <router-link v-if="bPrincipal" class="vSubItem" to='/' exact>Principal</router-link>
          <span v-if="bAfiliacion">&nbsp;</span>
          <router-link v-if="bAfiliacion" class="vSubItem" to='/Afiliacion' exact>Registro de POS</router-link>
          <span v-if="bConsultas">&nbsp;</span>
          <router-link v-if="bConsultas" class="vSubItem" to='/Consultas' exact>Consultas</router-link>
          <span v-if="bInventario">&nbsp;</span>
          <router-link v-if="bInventario" class="vSubItem" v-bind:db='this.db' to='/Pos' exact>Inventario de POS</router-link>
          <span v-if="false">&nbsp;</span>
          <router-link v-if="false" class="vSubItem" to='/Usuarios' exact>Gestin de Usuarios</router-link>
      </div>

      <router-view>esperando...</router-view>
    </div>
    <div v-else>
    
      <div class="vAppNoConectado">
        <img class="vendemasImgNone" src="./assets/vendemas.png">   
        <span class="vendemasInfo" @click="showModal = true"> Mas info </span>

        <modal v-if="showModal" @close="showModal = false">
          <!--
            you can use custom content here to overwrite
            default content
          -->
          <h3 slot="header">VendeMAS</h3>
          <div class="bodyModalPopup" slot="body">
            <p>Vendemas es una plataforma de procesamiento de pagos por tarjeta de crdito y dbito</p>
            <p>
            Tarifas:
                Costo de equipo: PEN  169.00
            </p>
            <article>
            Procedimiento:<br>
            1. Acercarse a ventanilla y solicitar la operacin VENDEMAS-POS. Cancelar PEN 169.00 <br>
            2. Con el voucher del pago, acercarse al promotor de VENDEMAS para su afiliacin.
            </article>
           </div> 
          <h3 slot="footer"></h3>
        </modal>

        <h2 class="txtMensaje" v-if="!autorizado" >{{ infoNoConectado }}</h2>
      </div>
    </div>
    
    <Piepagina></Piepagina>
  </div>
</template>

<script>
  import './fbApp.js';
  import axios from 'axios';
  import Cabecera from './components/Header.vue';
  import Consultas from './components/Consultas.vue';
  import Principal from './components/Principal.vue';
  import Pos from './components/Pos.vue';
  import Piepagina from './components/Footer.vue';
  //import Login from './components/Login.vue';
  import Afiliacion from './components/Afiliacion.vue';
  import modal from './components/modal.vue';

  //import Hello from './components/Hello.vue';
  //import SignUp from './components/SignUp.vue';
  //import firebase from 'firebase/app';
  import 'firebase/app';  
  import 'firebase/database';
  import 'firebase/firestore';
  import 'firebase/auth';

  //import Router from 'vue-router';
  import {mapMutations} from 'vuex';
  import 'bootstrap/dist/css/bootstrap.css';
  import 'bootstrap-vue/dist/bootstrap-vue.css';
  import fbApp from './fbApp.js';
  
export default {
    components: {Cabecera, Principal, Piepagina, Pos, Afiliacion, Consultas, modal},
    props:{ mostrar: Boolean },
    name: 'app',
    mounted(){
let iContainer = document.getElementById('app0');
      iContainer.addEventListener('mouseover', () => {
      //console.log(this.$refs.cabecera.$refs.counterComp);
      if (this.$refs.cabecera.$refs.counterComp){
        this.$refs.cabecera.$refs.counterComp.setHora();
      }
        

        //console.log('mouse over event');
      });

      var tt = this;
      this.auth.onAuthStateChanged(function (user) {
        if (user) {
            //console.info('Conectado: ', user);
            tt.$store.state.auth = true;
            tt.usuarioActivo = user;
            //console.log(user.email);
            var email = user.email;
            tt.$sEmail = email;


            var res = email.split("@");
            if (res[1]!= "gmail.com"){
              /*console.log('Dominio no vlido');*/
              tt.infoNoConectado = "Por favor utilice las credenciales correctas.";
              tt.fb.auth().signOut();
              //tt.$store.state.auth = false;
              //tt.usuarioActivo = null;
              tt.$router.push({ path: '/'});                          
            } else {
              
              //console.log(user.email);
              axios.post(tt.$root.$ls.get("IP") + '/vendemas/listausuario', {
                'sEmail': user.email
              })
              .then(function (response) {
                response.data.forEach(element => {
                  tt.$nomPerfil = element['nomPerfil'];
                  tt.$codAgencia = element['codOficina'];
                  tt.$nomAgencia = element['nomOficina'];

                  tt.$root.$data.codAgencia = element['codOficina'];

                  //console.log(element['codOficina']);
                  //console.log(tt.$root.$ls.get("stockMinimo"));
                  fbApp.firestore().collection("pos").where("agencia", "==", element['codOficina']).where("cliente","==","")
                      .onSnapshot(function(querySnapshot) { 
                          tt.$root.$data.sstock = querySnapshot.size;
                          //console.log("Se ha superado el stock Minimo");
                          if (querySnapshot.size < tt.$root.$ls.get("stockMinimo")){
                            //console.log(tt.$root.$ls.get("IP") + '/wsutils/sendmail');
                            var yyy = tt.$root.$ls.get("sEmailBody");
                            yyy = yyy.replace("$1", querySnapshot.size.toString());
                           axios.post(tt.$root.$ls.get("IP") + '/wsutils/sendmail', {
                              'user': 'no-reply@gmail.com',
                              'pwd': 'Gmail2017',
                              'recipient': tt.$root.$ls.get("sEmailMKT"),
                              'subject': '[StockMinimo POS] Agencia: ' + tt.$nomAgencia ,
                              'body': yyy
                            })
                            .then(function (response) {
                              console.log("Email enviado.");
                              });
                          }


                          
                          //tt.$forceUpdate();
                          //tt.Principal.stockM = "querySnapshot.size";
                      });                  

                  axios.post(tt.$root.$ls.get("IP")  + '/vendemas/perfilusuario', {
                    'sEmail': user.email,
                    'sProducto': 'VENDEMAS'
                  })
                  .then(function (response) {
                    response.data.forEach(item => {
                      switch (item['vRuta']){
                        case 'Principal':
                          tt.bPrincipal = true;
                          break;
                        case 'Afiliacion':
                          tt.bAfiliacion = true;
                          break;
                        case 'Consultas':
                          tt.bConsultas = true;
                          break;
                        case 'Inventario':
                          tt.bInventario = true;
                          break;                        
                      }
                    });

                    /*
                    console.log(response.data["sNombres"]);
                    console.log(response.data['sDNI']);*/
                  })
                  .catch();  



                });

                /*
                console.log(response.data["sNombres"]);
                console.log(response.data['sDNI']);*/
              })
              .catch();  

              tt.$router.push({ path: '/'}); 
            }
            //console.log(user.displayName);
            //console.log("USER", user.displayName);
            tt.displayName = user.displayName;
            tt.photo = user.photoURL;
            //console.log(user.photoURL);
            //console.log(this.usuarioActivo);
        } else {
            //console.warn('No conectado');
            tt.$store.state.auth = false;
            tt.usuarioActivo = null;
            tt.$router.push({ path: '/'});            
        }
      });             
    },
    computed: {
      conectado(){
        if (this.$store.state.auth){
          return this.displayName
        }else{        
          return "Invitado.";
        }
      },
      conectadoPhoto(){
        if (this.$store.state.auth){
          return this.photo
        }else{
          return "";
        }
      },
      autorizado() {
        return this.$store.state.auth;    
      }
    },
    data () {
      return {
        autenticado: false,
        haCargado:false,
        bPrincipal:false,
        bAfiliacion:false,
        bConsultas:false,
        bInventario:false,
        showModal:false,
        infoNoConectado: '',
        fb: null,
        db:null,
        auth:null,
        config: {
          apiKey: "AIzaSyAZIwxTNvpHVvwkJ4o2JKS2SpYHOLBeZsg",
          authDomain: "vende-mas.firebaseapp.com",
          databaseURL: "https://vende-mas.firebaseio.com",
          projectId: "vende-mas",
          storageBucket: "vende-mas.appspot.com",
          messagingSenderId: "699209203300"
        },
        usuarioActivo: [],
        displayName: '',
        photo: ''  
      }
    },
    methods: 
    mapMutations(['cambiar']),
    created() {
      this.fb = fbApp;
      /*
      this.fb = firebase;
      this.fb.initializeApp(this.config);
      */
      this.db = this.fb.database();
      this.auth = this.fb.auth();
    }

  }


</script>

