<template>
 <div class="vFormConsultas">
  <div class="vPOS">
            <form class="formPOS" v-on:submit.prevent="Agregar()">

        <fieldset>
                <legend>Elija el tipo de consulta</legend>
                <div class="row">
                <div class="col-md-6">
                    <input type="radio" v-model="consulta" value="dni" id="dni" checked  @change="cambia0()"/>
                    <label for="dni">&nbsp;DNI</label>
                </div>
                <div class="col-md-6">
                    <input type="radio" v-model="consulta" value="voucher" id="voucher"  @change="cambia0()"/>
                    <label for="voucher">&nbsp;POS</label>
                </div>
                  <div v-if="consulta === 'dni'">
                    <div class="form-group">
                        <input ref="serial" v-model="dni"
                                class="form-control serialPOS"
                                maxlength="8"
                                @keydown = "validaNum2" 
                                @keypress = "validaNum" 
                                placeholder="Nmero DNI"
                                type="text"/>
                    </div>

                  </div>
                  <div v-if="consulta === 'voucher'">
                    <div class="form-group">
                        <select class="form-control selectvoucher" 
                            v-model="arrPos.agencia" @change="cargarPOS(arrPos.agencia)" >
                            <option selected value="000">Elija la agencia</option>
                            <option v-for="item in Agencias" :key="item.nom_ofi" @change="cambia0()"
                                    :value="item.cod_ofi">{{ item.cod_ofi }} - {{ item.nom_ofi }}
                            </option>
                        </select>              
                    </div>
                  </div>
                   
                </div>
            </fieldset>      
          <input type="submit" class="btn btn-success" value="Buscar"/>
      </form>
        <modal v-if="showModal" @close="showModal = false">
          <h3 slot="header">Validacin en Consultas</h3>
          <div class="bodyModalPopup" slot="body">
            <h2>DNI</h2>
            <article>
                El cliente no tiene cuentas de ahorro (700 - Ahorro Soles) para afiliar a   l sistema
            </article>
           </div> 
          <h3 slot="footer"></h3>
        </modal>

      <div class="card listaTabla">
          <div class="card-header">
              <h3 v-if="consulta === 'dni'" class="card-title">Listado de DNI</h3>
              <h3 v-if="consulta === 'voucher'" class="card-title">Listado de POS por Agencia</h3>
          </div>
          <div class="card-body">
              <table class="table table-strip">
                  <thead>
                      <tr v-if="consulta === 'dni'" >
                          <th>DNI</th>
                          <th>Nombres</th>
                          <th>Cuentas</th>
                      </tr>
                      <tr v-if="consulta === 'voucher'" >
                          <th>Serie</th>
                          <th>Agencia</th>
                          <th>Fecha</th>
                      </tr>
                  </thead>
                  <tbody>
                      <tr v-for="(item, id, index) in lista" :key="index" :index="id">
                        <td>
                            {{ item.dni }}
                        </td>
                        <td>
                            {{ item.nombres }}
                        </td>
                        <td>
                            {{ item.cuenta }}
                        </td>                        
                      </tr>
                  </tbody>
              </table>
          </div>
      </div>

  </div>
 </div>
</template>


<script>
import fbApp from '../fbApp.js';
import axios from 'axios';
import modal from './modal.vue';
/*let db = fbApp.database();
let listadoPos0= db.ref('pos');
*/

let dbF = fbApp.firestore();
const sett = {timestampsInSnapshots: true};
dbF.settings(sett);
let listadoAgencia = dbF.collection('agencias');
let listadoPos = dbF.collection('pos');


export default {
  components: { modal },
  name: 'Pos',
  todo:'',
  firestore:{
    lstAgencias : listadoAgencia,
    lstPos11 : listadoPos
  },
  mounted(){
    var xxx = this;
    fbApp.firestore().collection("agencias").where("estado", "==", true)
        .get()
        .then(function(querySnapshot) {            
            var tList = [];
            querySnapshot.forEach(function(doc) {
                tList.push(doc.data());
            });
            xxx.Agencias = tList;
        })
        .catch(/*function(error) {
            console.log("Error getting documents: ", error);
        }*/);

  },  
  data () {
    return {      
      consulta:'dni',
      showModal:false,
      lista: [],
      dni: '',
      arrPos:{
        dni: '',
        nombres: '',
        cuenta: '',
        cliente: '',
        fecha: '',
        estado: ''
      },
      Agencias:[],
      listado02: listadoPos,
      lstAgencias: listadoAgencia
      }
  },
  methods: {
    cambia0: function(){
      this.lista = [];
    },
    validaNum2(event){
      var s = event.keyCode || event.charCode
      /*
      var c = event.which,//c dfgdfgdfgdfg
              d = event.keyCode,
              e = String.fromCharCode(c).toLowerCase(),
              f = "0123456789";
        (-1 != f.indexOf(e) || 9 == d || 37 != c && 37 == d || 39 == d && 39 != c || 8 == d || 46 == d && 46 != c) && 161 != c || event.preventDefault()
      */
      //console.log(s);
      if (s == 190 || s == 222){
        event.preventDefault(true);
        event.stopPropagation(true);
        event.stopped = true;  
      }

      if (event.defaultPrevented) {
        return; // Should do nothing if the default action has been cancelled
      }

      var handled = false;
      if (event.key !== undefined) {
        // Handle the event with KeyboardEvent.key and set handled true.
      } else if (event.keyIdentifier !== undefined) {
        // Handle the event with KeyboardEvent.keyIdentifier and set handled true.
      } else if (event.keyCode !== undefined) {
        // Handle the event with KeyboardEvent.keyCode and set handled true.
      }

      if (handled) {
        // Suppress "double action" if event handled
        event.preventDefault();
      }      
      
    },
    validaNum(event){
      //console.log(event.keyCode || event.charCode);
      //console.log(event.which);
      var s = event.keyCode || event.charCode
      switch (s){
        case 46:
        case 13:
        case 8:
        case 35:
        case 36:
        case 37:
        case 38:
        case 39:
        case 40:
        case 48:
        case 49:
        case 50:
        case 51:
        case 52:
        case 53:
        case 54:
        case 55:
        case 56:
        case 57:        
          break;
        default:
          event.preventDefault();
          event.stopPropagation();
          event.stopped = true;  
          break; 
      }
    },
    cargarPOS: function(agencia){
        //console.log(agencia);
        var xxx = this;
        if (agencia == "000"){
            fbApp.firestore().collection("pos").where("cliente", "==", "").orderBy("serial","asc")
            .onSnapshot(function(querySnapshot) { 
                var tList = [];
                querySnapshot.forEach(function(element) {
                    tList.push({"dni":element.data()['serial'], "nombres":element.data()["agencia"],
                            "cuenta": element.data()['fecha']});                    
                });
                xxx.lista = tList; 
            });
        }else{
        fbApp.firestore().collection("pos").where("agencia", "==", agencia).where("cliente", "==", "").orderBy("serial","asc")
        .onSnapshot(function(querySnapshot) { 
                var tList = [];
                querySnapshot.forEach(function(element) {
                    tList.push({"dni":element.data()["serial"], "nombres":element.data()["agencia"],
                            "cuenta": element.data()["fecha"]});                    
                });
                xxx.lista = tList;                   
        
        });}
    },      
    cargarDNI(){
      if (this.dni != "" &&  this.dni.length > 6){
        var mmm = this;
        this.lista = [];
        axios.post(mmm.$root.$ls.get("IP") + '/vendemas/consultacuentas', {
          sDNI: mmm.dni
        })
        .then(function (response) {
          //console.log(response.data);
          response.data.forEach(element => {
            mmm.lista.push({"dni":element['sDNI'], "nombres":element["sNombres"],
                            "cuenta": element['sCuenta'] + ' - ' + element['sNomProducto'] });              
          });
          if (mmm.lista.length < 1){
            mmm.showModal =true;
          }          

          /*
          console.log(response.data["sNombres"]);
          console.log(response.data['sDNI']);*/
        })
        .catch(/*function (error) {
          console.log(error);
        }*/);      
        //console.log(this.lista);
        //console.log(mmm.dni);

      }
    },      
    listaAgencias: function(agencia){
        //console.log("listaAgencias");
        var xxx = this;
        fbApp.firestore().collection("agencias").where("agencia", "==", agencia)
            .get()
            .then(function(querySnapshot) {            
                var tList = [];
                querySnapshot.forEach(function(doc) {
                    tList.push(doc.data());
                });
                xxx.Agencias = tList;
            })
            .catch(/*function(error) {
                console.log("Error getting documents: ", error);
            }*/);
    },
    Agregar: function() {
        //console.log("ok");
        if (this.consulta === "dni"){
            this.cargarDNI();
        }else{
            this.cargarPOS(this.arrPos.agencia);
        }

       /* listadoPos.push(this.arrPos);
        this.arrPos.serial = '2703280103000';
        this.arrPos.nomagencia = '';
        this.arrPos.cliente = '';
        this.arrPos.fecha ='';*/
        //console.log("listadoPos");

        /* Trick to reset/clear native browser form validation state */
        this.show = false;
        
        this.$nextTick(() => { this.show = true });
        //this.$refs.serial.$el.focus()

    },
    Eliminar: function(key) {
      //console.log("Eliminar", key[".key"]);
      //listadoPos.remove(key);
      var result = confirm("Seguro de eliminar el Serial \n" + key['serial']);
      if (result){
          //console.log("eliminado");
          //listadoPos.child(key['.key']).remove()
      }
      //alert("Seguro de eliminar el Serial " + key['serial']);
      
    },
    writeUserData: function(userId, name, email, imageUrl) {
        fbApp.firestore().collection('users').add({
            username: name,
            email: email,
            profile_picture : imageUrl
        });
    }      
  }

}
</script>



<style scoped>
 .selectvoucher{
    width: 21em;
    left: 6em;
    position: relative;
 }
 .btn-success{
    color: #fff;
    background-color: #28a745;
    /* border-color: #28a745; */
    margin-top: 13%;
    width: 7rem;
    height: 2.7rem;
 }
 .vFormConsultas{
    /*background-color: #77695e;*/
    color: white;
    width: 80%;
    position: relative;
    top: 0rem;
    left: 5%;
    /*margin: auto;*/
    height: 38em;     
 }
  .vPOS{
    top: 25px;
    left: 100px;
    margin: auto;
    height: 38em;
    position: inherit;
  }
  .piepagina {
    position: fixed;
    bottom: 0;
    padding-top: 2px;
    padding-left: 15px;
    width: 100%;
    height: 20px;
