    margin-left: -10px
  }
  .opcion {
      color: #fff;
      float:left;
      margin-left: 2%;
      margin-right: 2%;
  }  
  .opcion2 {
      color: cyan;
      /*margin-left: 2%;*/
      float:right;
      margin-right: 20px;
  }  
  .formPOS{
    display: inline-flex;      
  }
  .serialPOS{
    width: 19rem;
    left: 6rem;
    margin-right: 2rem;
    text-align: center;
    position: relative;
  }
  .card-body{
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    padding: 1.25rem;
    overflow: hidden;
        overflow-y: hidden;
    overflow-y: scroll;
    height: 22rem;
