
<template>
  <div class="vPOS">
      <form class="formPOS" v-on:submit.prevent="Agregar">
          <div class="form-group">
              <label for="">Nmero de Serie POS</label>
              <input ref="serial" v-model="arrPos.serial"
                     class="form-control serialPOS"
                     placeholder="Nmero de Serie"
                     type="text"
              />
          </div>
          <div class="form-group">
              <label for="">Sucursal</label>
            <select :disabled= 'isDisabled' v-model="sucursal" class="form-control"  @change= "Cambiar" >
                <option selected value="000">Elija la agencia</option>
                <option v-for="item in Agencias" :key="item.nom_ofi" :value="item.cod_ofi">{{ item.cod_ofi }} - {{ item.nom_ofi }}</option>
            </select>              
              
              
          </div>


          <input type="submit" class="btn btn-success btnAgregar" value="Agregar"/>
      </form>

      <div class="card listaTabla">
          <div class="card-header">
              <h3 class="card-title">Listado de POS</h3>
          </div>
          <div class="card-body">
              <table class="table table-strip">
                  <thead>
                      <tr>
                          <th>Serie</th>
                          <th>Agencia</th>
                          <th>Fec.Registro</th>
                          <th>Acciones</th>
                      </tr>
                  </thead>
                  <tbody>
                      <tr v-for="(item, id, index) in POS" :key="index" :index="id">
                        <td>
                            {{ item.serial }}
                        </td>
                        <td>
                            {{ item.agencia }}
                        </td>
                        <td>
                            {{ item.fecha }}
                        </td>                        
                        <td>
                            <button v-on:click.prevent="Eliminar(item)"> Eliminar</button> 
                        </td>                        
                      </tr>
                  </tbody>
              </table>
          </div>
      </div>

  </div>
</template>

<script>
import fbApp from '../fbApp.js';
let db = fbApp.database();

let listadoPos= db.ref('pos').equalTo('agencia','060');

let dbF = fbApp.firestore();
const sett = {timestampsInSnapshots: true};
dbF.settings(sett);
let listadoAgencia = dbF.collection('agencias');
let listadoPos1 = dbF.collection('pos');

export default {
  name: 'Pos',
  todo:'',
  firebase:{
    lista:  listadoPos
  },  
  firestore:{
    lstAgencias : listadoAgencia,
    lstPos: listadoPos1
  },
  mounted(){
    var xxx = this;    
    
    fbApp.firestore().collection("agencias").where("estado", "==", true)
        .get()
        .then(function(querySnapshot) {            
            var tList = [];
            querySnapshot.forEach(function(doc) {
                tList.push(doc.data());
            });
            xxx.Agencias = tList;
        })
        .catch(/*function(error) {
            console.log("Error getting documents: ", error);
        }*/);

    this.listaAgencias(this.$root.$data.codAgencia);
    
  },  
  data () {
    return {      
      isDisabled:false,
      arrPos:{
        serial: '2703280103000',
        agencia: '',
        nomagencia: '',
        cliente: '',
        fecha: '',
        estado: ''
      },
      sucursal: this.$root.$data.codAgencia,
      Agencias:[],
      POS: [],
      listado02: listadoPos,
      lstAgencias: listadoAgencia,
      msg: 'Gmail.com'
    }
  },
  methods: {
    listaAgencias: function(agencia){
        //console.log(agencia);
        var xxx = this;
        agencia = this.$root.$data.codAgencia;

        //fbApp.firestore().collection("pos").where("agencia", "==", agencia).where("cliente", "==", "").orderBy("serial","asc")
        fbApp.firestore().collection("pos").where("agencia", "==", agencia).orderBy("serial","asc")
        .onSnapshot(function(querySnapshot) { 
            var tList = [];
            querySnapshot.forEach(function(doc) {
                tList.push(doc.data());
            });
            xxx.POS = tList;    
            xxx.isDisabled =true;      
        });
    },
    Agregar: function() {
        var ooo = new Date();
        
        this.arrPos.agencia = this.sucursal;
        this.arrPos.fecha = ooo.toISOString().replace(/-/g, '').split('T')[0];

        listadoPos1.add(this.arrPos);
        this.arrPos.serial = '2703280103000';
        this.arrPos.nomagencia = '';
        this.arrPos.cliente = '';
        this.arrPos.fecha ='';
        

        /* Trick to reset/clear native browser form validation state */
        this.show = false;
        
        this.$nextTick(() => { this.show = true });
        //this.$refs.serial.$el.focus();

    },
    Eliminar: function(key) {
      var result = confirm("Seguro de eliminar el Serial \n" + key['serial']);
      if (result){
        fbApp.firestore().collection("pos").where("serial", "==", key["serial"])
                    .get()
                    .then(function(querySnapshot) {            
                        querySnapshot.forEach(function(doc) {
                            doc.ref.delete();
                        });
                    })
                    .catch(/*function(error) {
                        console.log("Error getting documents: ", error);
                    }*/);
      }
    },
    Cambiar: function(){
        //console.log(evt);
        //console.log(this.sucursal);
        this.listaAgencias(this.sucursal);
        //alert("todo", todo);
    },
    writeUserData: function(userId, name, email, imageUrl) {
        fbApp.firestore().collection('users').add({
            username: name,
            email: email,
            profile_picture : imageUrl
        });
    }      
  }

}
</script>

