    },
    autorizado() {
      return this.$store.state.auth;    
    }
  },
    methods: {
      signIn: function() {        
        firebase.auth().signInWithPopup(proveedor).catch(/*function (error) {
            //console.error('Error haciendo logIn: ', error);
        }*/);
      }, 
      logout: function() {
        var xxx = this;
        firebase.auth().signOut().then(() => {
          this.$parent.bPrincipal = false;
          this.$parent.bAfiliacion = false;
          this.$parent.bConsultas = false;
          this.$parent.bInventario = false;
          xxx.$refs.counterComp.setHora();
          //xxx.$root.$data.sSesion= "2018-08-31 00:00:00";
          //console.log(xxx.$root.$data.sSesion);
          //console.log("saliendo");
          this.$router.replace('login')
        })
      },
      myMethod: function(){
       this.$refs.counterComp.setHora();
      }
    }
  }
</script>

