import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router';
import VueFire from 'vuefire';
import { routes } from './routes';
import {store} from './store';
import BootstrapVue from 'bootstrap-vue'
import VueLocalStorage from 'vue-localstorage';
import fbApp from './fbApp.js';

let dbF = fbApp.firestore();
const sett = {timestampsInSnapshots: true};
dbF.settings(sett);

Vue.use(VueLocalStorage, {
  name: 'ls',
  bind: true //created computed members from your variable declarations
})


Vue.use(VueRouter)
Vue.use(BootstrapVue)
Vue.use(VueFire)
const router = new VueRouter({
  mode: 'history',
  routes,
 });


 router.beforeEach((to, from, next) => {
  if (store.state.auth){
    next();
  }else{
    if (to.fullPath != "/"){
      //console.log("Por favor, inicie sesin");
      next("/");
    }else{
      next(true);  
    }
  }
 });
 
Vue.prototype.$codAgencia = '';
Vue.prototype.$nomAgencia = '';
Vue.prototype.$nomPerfil = '';
Vue.prototype.$stock = 0;
Vue.prototype.$tiempo = 2;

Vue.config.productionTip = false

var time2 = new Date();
var time = new Date(time2.getTime() + Vue.prototype.$tiempo * 60000);
//console.log(time);
var yyyy = time.getFullYear();
var month1 = time.getMonth();
var month = (month1+1);
var days = time.getDate();

var hour = time.getHours();
var minute = time.getMinutes();
var second = time.getSeconds();
var temp = yyyy.toString() + ((month < 10) ? '-0' : '-') + month + ((days < 10) ? '-0' : '-') + days;
temp += (((hour < 10) ? ' 0' : ' ') + hour );
temp += ((minute < 10) ? ':0' : ':') + minute;
temp += ((second < 10) ? ':0' : ':') + second;

//console.log(temp);

//var xxx = new Date.now();
//console.log(xxx.format(""))

//export const vendeMasBus = new Vue();

new Vue({
  data:{
    sstock:0,
    sSesion: temp.toString(),
    codAgencia:'', 
    timeout: 15,
    mostrar: false  
  },
  methods: {
    escapeKeyListener: function(evt) {
      if (evt.keyCode === 13) {
        //this.modalOpen = false;
        //console.log("presiono ENTER");
      }
    }
  },
  beforeCreate: function(){
    var xxx = this;
    dbF.collection('config').get().then(function(querySnapshot) {                 
      querySnapshot.forEach(function(doc) {
         //console.log(doc.data()["urlAPI"]);     
         xxx.$ls.set("IP", doc.data()["urlAPI"]);
         xxx.$ls.set("VendeMas", doc.data()["urlVendeMas"]);
         xxx.$ls.set("timeout", doc.data()["timeout"]);
         xxx.mostrar=true;
      });
    });
  },
  mounted: function(){
    var xxx = this;
    dbF.collection('config').get().then(function(querySnapshot) {                 
      querySnapshot.forEach(function(doc) {
         //console.log(doc.data()["urlAPI"]);     
         xxx.$ls.set("IP", doc.data()["urlAPI"]);
         xxx.$ls.set("VendeMas", doc.data()["urlVendeMas"]);
         xxx.$ls.set("timeout", doc.data()["timeout"]);
         xxx.$ls.set("stockMinimo", doc.data()["stockMinimo"]);
         xxx.$ls.set("sEmailMKT", doc.data()["sEmailMKT"]);
         xxx.$ls.set("sEmailBody",doc.data()["sEmailBody"]);
         xxx.mostrar=true;
      });
    });    
  },
  created: function() {     
    document.addEventListener('keyup', this.escapeKeyListener);

  },
  destroyed: function() {
    document.removeEventListener('keyup', this.escapeKeyListener);
  },
  router,
  store,
  render: h => h(App)
}).$mount('#app')

