<script>
import fbApp from '../fbApp.js';

let dbF = fbApp.firestore();
const sett = {timestampsInSnapshots: true};
dbF.settings(sett);
let listadoVoucher = dbF.collection('voucher');

          


//import Router from 'vue-router';

//import {mapMutations} from 'vuex';
import axios from 'axios';
export default {
  name: 'Principal',
  data () {
    return {
      msg: 'Stock de POS',
      precio: 169.00,
      datos: [],
      show: false
    }
  },
  beforeCreate() {
    //console.log(this.$root.$data.restIP);
    //console.log(this.$root.$ls.get("IP") + '/vendemas/listavoucher');
    axios.post(this.$root.$ls.get("IP") + '/vendemas/listavoucher')
    .then(response => {
        response.data.forEach( function(valor) {
           listadoVoucher.where("sNumTRX", "==",valor["sNumTRX"]).get().then(
             function(querySnapshot) {      
                if (querySnapshot.size < 1){
                  
                  listadoVoucher.add(valor);                  
                }
            })
            .catch(); 
        });       
    })
    .catch(e => {
      this.errors.push(e);
    })
  },
  mounted(){
    //console.log(this.$root.$ls.get("IP"));
    /*
    console.log(this.$codAgencia);
    var tt = this;
    fbApp.firestore().collection("pos").where("agencia", "==", this.$codAgencia).where("cliente","==","")
        .onSnapshot(function(querySnapshot) { 
            console.log(querySnapshot.size);
            tt.stock = querySnapshot.size;          
        });  
    */
    //this.stock = 10;

    //console.log(this.$store.state);

  },
  computed: {
   pct(){
     //console.log(this.$root.$data.sstock);
     return Math.round(1.0 * this.$root.$data.sstock / 20 * 100);
   },
   size(){
    //console.log(this.pct);
    return "pie-wrapper pie-wrapper--solid progress-" + this.pct;
   },
   dniValidado(){
    return this.form.dni + '00';
   },
   stock1(){
    return this.$root.$data.sstock;
   }
  },
  methods:{
    onSubmit() {
        alert("todo OK");
    }
  }
 }
</script>


