<template>
    <ul class="vuejs-countdown">
        <!-- <li v-if="days > 0">
            <p class="digit">{{ days | twoDigits }}</p>
            <p class="text">{{ days > 1 ? 'days' : 'day' }}</p>
        </li>
        <li>
            <p class="digit">{{ hours | twoDigits }}</p>
            <p class="text">{{ hours > 1 ? 'hours' : 'hour' }}</p>
        </li>-->
        <li>
            <p class="digit">{{ minutes | twoDigits }}</p>
            <!-- <p class="text">min</p> -->
        </li>
        <li>
            <p class="digit">{{ seconds | twoDigits }}</p>
            <!-- <p class="text">Sec</p> -->
        </li>
    </ul>
</template>

<script>

let interval = null;

export default {
    name: 'Counter',
    props: {
        deadline: {
            type: String
        },
        end: {
            type: String
        },
        stop: {
            type: Boolean
        }, 
        diferencia:{
            type: Number
        }
    },
    data() {
        return {
            now: Math.trunc((new Date()).getTime() / 1000),
            date: null,
            diff: 0,
            temp: 0,
            deadline1: "2099-08-31"
        }
    },   
    mounted(){
        this.setHora();
    },
    created() {
        if (!this.deadline && !this.end) {
            throw new Error("Missing props 'deadline' or 'end'");
        }

        let endTime = this.deadline? this.deadline : this.end;
        //console.log(endTime);
        this.date = Math.trunc(Date.parse(endTime.replace(/-/g, "/")) / 1000);

        if (!this.date) {
            throw new Error("Invalid props value, correct the 'deadline' or 'end'");
        }

        interval = setInterval(() => {
            this.now = Math.trunc((new Date()).getTime() / 1000);
        }, 1000);
    },
    computed: {
        seconds() {
            return Math.trunc(this.diff) % 60
        },

        minutes() {
            return Math.trunc(this.diff / 60) % 60
        },

        hours() {
            return Math.trunc(this.diff / 60 / 60) % 24
        },

        days() {
            return Math.trunc(this.diff / 60 / 60 / 24)
        }
    },
    watch: {
        now() {
            this.diff = this.date - this.now;
            //console.log(this.diff);
            if(this.diff <= 0 || this.stop){
                this.diff = 0;
                this.cerrarSesion();
                
                // Remove interval
                clearInterval(interval);
            }
        }
    },
    filters: {
        twoDigits(value) {
            if ( value.toString().length <= 1 ) {
                return '0'+value.toString()
            }
            return value.toString()
        }
    },
    methods:{
        setHora(){
            var time2 = new Date();
            var time = new Date(time2.getTime() + (this.$root.$ls.get("timeout") * 60000));
            
            var yyyy = time.getFullYear();
            var month1 = time.getMonth();
            var month = (month1+1);
            var days = time.getDate();

            var hour = time.getHours();
            var minute = time.getMinutes();
            var second = time.getSeconds();
            this.temp = yyyy.toString() + ((month < 10) ? '-0' : '-') + month + ((days < 10) ? '-0' : '-') + days;
            this.temp += (((hour < 10) ? ' 0' : ' ') + hour );
            this.temp += ((minute < 10) ? ':0' : ':') + minute;
            this.temp += ((second < 10) ? ':0' : ':') + second;
            this.deadline1 = this.temp;
            this.date = Math.trunc(Date.parse(this.deadline1.replace(/-/g, "/")) / 1000);

        },
        cerrarSesion(){
            this.$parent.logout();
        }
    },
    destroyed() {
        clearInterval(interval);
    }
}
</script>
<style>
.vuejs-countdown {
    position: absolute;
    right: 8em;
    top: 2em;    
    margin: 0;
}
.vuejs-countdown li {
  display: inline-block;
  margin: 0 2px;
  text-align: center;
  position: relative;
}
.vuejs-countdown li p {
    margin: 0;
}
.vuejs-countdown li:after {
  content: ":";
  position: absolute;
  top: -1px;
  right: -4px;
  font-size: 14px;
}
.vuejs-countdown li:first-of-type {
  margin-left: 0;
}
.vuejs-countdown li:last-of-type {
  margin-right: 0;
}
.vuejs-countdown li:last-of-type:after {
  content: "";
}
.vuejs-countdown .digit {
  font-size: 14px;
  font-weight: 600;
  line-height: 1.4;
  margin-bottom: 0;
}
.vuejs-countdown .text {
  text-transform: uppercase;
  margin-bottom: 0;
  font-size: 10px;
}
</style>